.buttonAdmin {
    background-color: black;
    color: white;
    font-size: 30px;
    padding: 1.5%;
}

.modificar{
    background-color: aquamarine;
}
.blockAdmin {
    background-color: black;
}


.cardProductos {
    padding: 5rem 1rem;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(21rem, 2fr));
    gap: 0rem 2rem;
    box-sizing: border-box;
}

.cardProducto {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    border: 4px solid transparent;
    border-radius: 7px;
    box-sizing: content-box;
}

.cardProducto:hover {
    border: 4px solid transparent;
    box-shadow: 1px 10px 10px 2px rgba(90, 94, 95, 0.873);
    
} 

.producto_img img{
    width: 250px;
    height: 290px;
}

.contenidoProd {
    padding: 1rem;
}

.h7 {
    font-weight: 500;
    font-size: 1.5rem;
    color: #1a202c;
    font-family: Helvetica, sans-serif;
}

.btn {
    width: 100%;
    height: 45px;
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 1rem 2rem;
    border: none;
    outline: 0;
    cursor: pointer;
    border-radius: 5px;
    background-color: rgb(248, 248, 249);
    color: black;
}

.btn:hover {
    background-color: rgb(91, 176, 206);
    color: rgb(252, 252, 254);

}




